<template>
  <div style="margin-top: 10px">
    <div style="display: flex;justify-content: space-between;align-items: center;padding-left: 0px">
      <el-button icon="el-icon-plus"
                 size="mini"
                 type="primary"
                 @click="showAddSalaryView">Ê∑ªÂä†Â∑•ËµÑË¥¶Â•ó
      </el-button>
      <el-button icon="el-icon-refresh" size="mini" type="success"
                 @click="initSalaries"></el-button>
    </div>
    <el-table :data="salaries" border stripe v-loading="loading"
              @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="50"/>
      <el-table-column width="120" prop="name" label="Ë¥¶Â•óÂêçÁß∞"></el-table-column>
      <el-table-column width="70" prop="basicSalary" label="Âü∫Êú¨Â∑•ËµÑ"></el-table-column>
      <el-table-column width="70" prop="trafficSalary" label="‰∫§ÈÄöË°•Âä©"></el-table-column>
      <el-table-column width="70" prop="lunchSalary" label="ÂçàÈ§êË°•Âä©"></el-table-column>
      <el-table-column width="70" prop="bonus" label="Â•ñÈáë"></el-table-column>
      <el-table-column width="100" prop="createDate" label="ÂêØÁî®Êó∂Èó¥"></el-table-column>
      <el-table-column label="ÂÖªËÄÅÈáë" align="center">
        <el-table-column width="70" prop="pensionPer" label="ÊØîÁéá"></el-table-column>
        <el-table-column width="70" prop="pensionBase" label="Âü∫Êï∞"></el-table-column>
      </el-table-column>
      <el-table-column label="ÂåªÁñó‰øùÈô©" align="center">
        <el-table-column width="70" prop="medicalPer" label="ÊØîÁéá"></el-table-column>
        <el-table-column width="70" prop="medicalBase" label="Âü∫Êï∞"></el-table-column>
      </el-table-column>
      <el-table-column label="ÂÖ¨ÁßØÈáë" align="center">
        <el-table-column width="70" prop="accumulationFundPer" label="ÊØîÁéá"></el-table-column>
        <el-table-column width="70" prop="accumulationFundBase" label="Âü∫Êï∞"></el-table-column>
      </el-table-column>
      <el-table-column label="Êìç‰Ωú" align="center">
        <template slot-scope="scope">
          <el-button @click="showEditSalaryView(scope.row)">ÁºñËæë</el-button>
          <el-button type="danger" @click="deleteSalary(scope.row)">Âà†Èô§</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="margin-top: 10px;display: flex;justify-content: space-between">
      <el-button :disabled="multipleSelection.length<=0" round @click="multipleDelete" size="mini"
                 type="danger">ÊâπÈáèÂà†Èô§
      </el-button>
    </div>
    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogVisible"
      width="50%">
      <div style="display: flex;justify-content: space-around;align-items: center">
        <el-steps direction="vertical" :active="activeItemIndex">
          <el-step :title="itemName" v-for="(itemName,index) in salaryItemName"
                   :key="index"></el-step>
        </el-steps>
        <el-input v-model="salary[title]" :placeholder="'ËØ∑ËæìÂÖ•'+salaryItemName[index]+'...'"
                  v-for="(value,title,index) in salary"
                  :key="index" v-show="activeItemIndex===index" style="width: 200px"></el-input>
      </div>
      <span slot="footer" class="dialog-footer">
    <el-button @click="preStep">{{activeItemIndex===10?'ÂèñÊ∂à':'‰∏ä‰∏ÄÊ≠•'}}</el-button>
    <el-button type="primary" @keydown.enter.native="nextStep" @click="nextStep">{{activeItemIndex===10?'ÂÆåÊàê':'‰∏ã‰∏ÄÊ≠•'}}</el-button>
  </span>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "SalSob",
    data() {
      return {
        dialogVisible: false,
        loading: true,
        multipleSelection: [],
        dialogTitle: 'Ê∑ªÂä†Â∑•ËµÑË¥¶Â•ó',
        salaries: [],
        activeItemIndex: 0,
        salaryItemName: [
          'Âü∫Êú¨Â∑•ËµÑ',
          '‰∫§ÈÄöË°•Âä©',
          'ÂçàÈ§êË°•Âä©',
          'Â•ñÈáë',
          'ÂÖªËÄÅÈáëÊØîÁéá',
          'ÂÖªËÄÅÈáëÂü∫Êï∞',
          'ÂåªÁñó‰øùÈô©ÊØîÁéá',
          'ÂåªÁñó‰øùÈô©Âü∫Êï∞',
          'ÂÖ¨ÁßØÈáëÊØîÁéá',
          'ÂÖ¨ÁßØÈáëÂü∫Êï∞',
          'Ë¥¶Â•óÂêçÁß∞'
        ],
        salary: {
          basicSalary: 0,
          trafficSalary: 0,
          lunchSalary: 0,
          bonus: 0,
          pensionPer: 0,
          pensionBase: 0,
          medicalPer: 0,
          medicalBase: 0,
          accumulationFundPer: 0,
          accumulationFundBase: 0,
          name: ''
        }
      }
    },
    mounted() {
      this.initSalaries();
    },
    methods: {
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      multipleDelete() {
        //ÈÅçÂéÜÂèÇÊï∞
        let ids = '';
        this.multipleSelection.forEach(slices => {
          ids += (slices.id + ','
          )
        });
        this.deleteSalaries(ids);
      },
      showEditSalaryView(data) {
        this.dialogTitle = '‰øÆÊîπÂ∑•ËµÑË¥¶Â•ó';
        this.dialogVisible = true;
        this.salary.basicSalary = data.basicSalary;
        this.salary.trafficSalary = data.trafficSalary;
        this.salary.lunchSalary = data.lunchSalary;
        this.salary.bonus = data.bonus;
        this.salary.pensionPer = data.pensionPer;
        this.salary.pensionBase = data.pensionBase;
        this.salary.medicalPer = data.medicalPer;
        this.salary.medicalBase = data.medicalBase;
        this.salary.accumulationFundPer = data.accumulationFundPer;
        this.salary.accumulationFundBase = data.accumulationFundBase;
        this.salary.name = data.name;
        this.salary.id = data.id;
      },
      deleteSalaries(ids) {
        this.$confirm('Á°ÆËÆ§Âà†Èô§?', 'ÊèêÁ§∫', {
          confirmButtonText: 'Á°ÆÂÆö',
          cancelButtonText: 'ÂèñÊ∂à',
          type: 'warning'
        }).then(() => {
          this.deleteRequest("/sal/sob/" + ids).then(resp => {
            if (resp && resp.status === 200) {
              //200ÂàôÂàùÂßãÂåñË°®Ê†ºÔºåÂà∑Êñ∞Êï∞ÊçÆ
              this.initSalaries();
            }
          })

        }).catch(() => {
          this.$message({
            type: 'info',
            message: 'Â∑≤ÂèñÊ∂àÂà†Èô§'
          });
        });
      },
      deleteSalary(data) {
        this.$confirm('Ê≠§Êìç‰ΩúÂ∞ÜÂà†Èô§„Äê' + data.name + '„ÄëË¥¶Â•óÔºåÊòØÂê¶ÁªßÁª≠Ôºü', 'ÊèêÁ§∫', {
          cancelButtonText: 'ÂèñÊ∂à',
          confirmButtonText: 'Á°ÆÂÆö'
        }).then(() => {
          this.deleteRequest("/sal/sob/" + data.id).then(resp => {
            if (resp) {
              this.initSalaries();
            }
          })
        }).catch(() => {
          this.$message.info("ÂèñÊ∂àÂà†Èô§!");
        })
      },
      preStep() {
        if (this.activeItemIndex === 0) {
          return;
        } else if (this.activeItemIndex === 10) {
          //ÂÖ≥Èó≠ÂØπËØùÊ°Ü
          this.dialogVisible = false;
          return;
        }
        this.activeItemIndex--;
      },
      nextStep() {
        if (this.activeItemIndex === 10) {
          if (this.salary.id) {
            this.putRequest("/sal/sob/", this.salary).then(resp => {
              if (resp) {
                this.initSalaries();
                this.dialogVisible = false;
              }
            })
          } else {
            this.postRequest("/sal/sob/", this.salary).then(resp => {
              if (resp) {
                this.initSalaries();
                this.dialogVisible = false;
              }
            });
          }
          return;
        }
        this.activeItemIndex++;
      },
      showAddSalaryView() {
        //Êï∞ÊçÆÂàùÂßãÂåñ
        this.salary = {
          basicSalary: 0,
          trafficSalary: 0,
          lunchSalary: 0,
          bonus: 0,
          pensionPer: 0,
          pensionBase: 0,
          medicalPer: 0,
          medicalBase: 0,
          accumulationFundPer: 0,
          accumulationFundBase: 0,
          name: ''
        }
        this.dialogTitle = 'Ê∑ªÂä†Â∑•ËµÑË¥¶Â•ó';
        this.activeItemIndex = 0;
        this.dialogVisible = true;
      },
      initSalaries() {
        this.loading = true;
        this.getRequest("/sal/sob/").then(resp => {
          if (resp) {
            this.salaries = resp;
            this.loading = false;
          }
        })
      }
    }
  }
</script>

