      </el-table-column>
      <el-table-column
        align="center"
        label="ÈÉ®Èó®ÂêçÁß∞">
        <el-table-column
          label="ÂëòÂ∑•ÂßìÂêç"
          prop="name"
          width="120"/>
        <el-table-column
          label="Âü∫Êú¨Â∑•ËµÑ"
          prop="salary.basicSalary"
          width="120"/>
        <el-table-column
          label="ÂëòÂ∑•Â•ñÈáë"
          width="120">
          <template slot-scope="scope">{{scope.row.adjustSalary?scope.row.adjustSalary.amount:0}}</template>
        </el-table-column>
        <el-table-column
          label="ÈÉ®Èó®Â•ñÈáë"
          prop="salary.bonus"
          width="120"/>
        <el-table-column
          label="ÂçàÈ§êË°•Âä©"
          prop="salary.lunchSalary"
          width="120"/>
        <el-table-column
          label="‰∫§ÈÄöË°•Âä©"
          prop="salary.trafficSalary"
          width="120"/>
        <el-table-column
          label="ÂÖªËÄÅÈáëÂü∫Êï∞"
          prop="salary.pensionBase"
          width="120"/>
        <el-table-column
          label="ÂÖªËÄÅÈáëÊØîÁéá"
          prop="salary.pensionPer"
          width="120"/>
        <el-table-column
          label="ÂåªÁñó‰øùÈô©Âü∫Êï∞"
          prop="salary.medicalBase"
          width="120"/>
        <el-table-column
          label="ÂÖªËÄÅ‰øùÈô©ÊØîÁéá"
          prop="salary.medicalPer"
          width="120"/>
        <el-table-column
          label="ÂÖ¨ÁßØÈáëÂü∫Êï∞"
          prop="salary.accumulationFundBase"
          width="120"/>
        <el-table-column
          label="ÂÖ¨ÁßØÈáëÊØîÁéá"
          prop="salary.accumulationFundPer"
          width="120"/>
        <el-table-column
          label="ÈÖçÁΩÆË¥¶Â•óÂêçÁß∞"
          prop="salary.name"
          width="120"/>
        <el-table-column
          label="Â∫îÂèëÂ∑•ËµÑ(Êú™ÁÆóÂÖ•Â•ñÈáë)"
          prop="salary.allSalary"
          width="120"/>
        <el-table-column
          label="Â∫îÂèëÂ∑•ËµÑ(ÁÆóÂÖ•Â•ñÈáë)"
          prop="salaryWithBonus"
          width="120"/>
      </el-table-column>
    </el-table>
    <div style="margin-right:20px;display: flex;justify-content: center">
      <el-pagination
        :page-size="1"
        :total="total"
        @current-change="currentPageChanged"
        :current-page.sync="page"
        @size-change="sizeChanged"
        layout="total, sizes, prev, pager, next, jumper">
      </el-pagination>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        loading: true,
        depId: '',
        deps: [],
        depSalary: [],
        page: 1,
        size: 10,
        total: 0
      }
    },
    methods: {
      initTable() {
        let url = '/sal/table/?page=' + this.page + '&size=' + this.size + '&depId=' + this.depId;
        if (this.depId !== '') {
          this.loading = true;
          this.getRequest(url).then(resp => {
            if (resp) {
              this.total = resp.total;
              this.depSalary = resp.data;
              this.loading = false;
            }
          })
        } else {
          this.loading = false;
        }
      },
      exportXls() {
        //‰ªéÁà∂Á™óÂè£ÊâìÂºÄËÄå‰∏çÊòØÂ∞èÁ™óÂè£ÊâìÂºÄ ËøõË°å‰∏ãËΩΩ
        let url = '/sal/table/export?depId=' + this.depId;
        window.open(url, '_parent');
      },
      currentPageChanged(cPage) {
        this.page = cPage;
        this.initTable();
      },
      sizeChanged(cSize) {
        this.size = cSize;
        this.initTable();
      }
    },
    mounted: function () {
      //ÂÖà‰ªésessionStorageÂà§Êñ≠‰Ω†ÊòØÂê¶Â≠òÂú®ÈÉ®Èó®‰ø°ÊÅØÔºåÊ≤°Ëé∑ÂèñÂà∞ÂÜçËØ∑Ê±Ç
      if (!window.sessionStorage.getItem("depList")) {
        this.getRequest('/dep/list').then(resp => {
          if (resp) {
            this.deps = resp;
            window.sessionStorage.setItem("depList", JSON.stringify(resp));
          }
        })
      }
      //Â≠òÂÖ•sessionStorage
      this.deps = JSON.parse(window.sessionStorage.getItem("depList"));
      this.initTable();
    }
  }
</script>
