<template>
  <div style="margin-top: 10px">
    <div
      style="display: flex;justify-content: space-between;align-items: center;padding-left: 0px">
      <el-button @click="addAdjustSalaryInfo" icon="el-icon-plus" type="primary" size="mini">
        Ê∑ªÂä†Â•ñÊÉ©ËÆ∞ÂΩï
      </el-button>

      <div style="display: inline">
        <el-tag size="medium">ËØ∑ËæìÂÖ•ÂëòÂ∑•ÂßìÂêç:</el-tag>
        <el-input
          placeholder="ÈÄöËøáÂëòÂ∑•ÂêçÊêúÁ¥¢ÂëòÂ∑•,ËÆ∞ÂæóÂõûËΩ¶Âì¶..."
          clearable
          @change="keywordsChange"
          style="width: 300px;margin: 0px;padding: 0px;"
          size="mini"
          @keyup.enter.native="search"
          prefix-icon="el-icon-search"
          v-model="keywords">
        </el-input>
        <el-button type="primary" size="mini" style="margin-left: 5px" icon="el-icon-search"
                   @click="search">ÊêúÁ¥¢
        </el-button>

      </div>

      <el-button size="mini" type="success" @click="loadAdjustSalaryInfo"
                 icon="el-icon-refresh"></el-button>
    </div>
    <div style="padding-left: 0px;padding-top: 0px">
      <div>
        <el-table
          :data="adjusts"
          stripe
          v-loading="tableLoading"
          size="mini"
          @selection-change="handleSelectionChange"
          style="width: 100%">
          <el-table-column
            type="selection"
            width="40">
          </el-table-column>
          <el-table-column
            prop="employee.name"
            label="ÂëòÂ∑•ÂßìÂêç">
          </el-table-column>
          <el-table-column
            label="Â•ñÊÉ©Êó•Êúü">
            <template slot-scope="scope">{{ scope.row.asDate}}</template>
          </el-table-column>
          <el-table-column
            width="200"
            prop="amount"
            label="Â•ñÊÉ©ÈáëÈ¢ù">
          </el-table-column>
          <el-table-column
            prop="reason"
            label="Â•ñÊÉ©ÂéüÂõ†">
          </el-table-column>
          <el-table-column
            prop="remark"
            label="Â§áÊ≥®">
          </el-table-column>
          <el-table-column label="Êìç‰Ωú" align="center">
            <el-table-column label="ÁºñËæë" align="center">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  @click="handleEdit(scope.$index, scope.row)">ÁºñËæë
                </el-button>
              </template>
            </el-table-column>
            <el-table-column label="Âà†Èô§" align="center">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="danger"
                  @click="handleDelete(scope.$index, scope.row)">Âà†Èô§
                </el-button>
              </template>
            </el-table-column>
          </el-table-column>
        </el-table>
      </div>
      <div style="display: flex;justify-content: space-between;margin: 2px" v-if="adjust!==0">
        <el-button type="danger" round size="mini" :disabled="multipleSelection.length===0"
                   @click="deleteAll">ÊâπÈáèÂà†Èô§
        </el-button>
        <el-pagination
          background
          :page-size="size"
          :current-page="currentPage"
          @current-change="currentChange"
          layout="total,prev, pager, next"
          :total="totalCount">
        </el-pagination>
      </div>
    </div>
    <div style="text-align: left">
      <el-form :model="adjust" :rules="rules" ref="addAdjustSalary"
               style="margin: 0px;padding: 0px;">
        <!--        NOTICE dialog Â•óÂú®divÈáåÈù¢‰ΩøÁî®Êó∂Èó¥Êéß‰ª∂‰ºöÊä•Èîô-->
        <el-dialog
          :title="dialogTitle"
          style="padding: 0px;"
          :close-on-click-modal="false"
          :visible.sync="dialogVisible"
          width="77%">
          <!--Á¨¨‰∏ÄË°å-->
          <el-row>
            <el-col :span="6" v-if="this.dialogTitle==='Êñ∞Â¢ûÂ•ñÊÉ©'">
              <div>
                <el-form-item label="ÂßìÂêç:" prop="eid">
                  <el-tooltip class="item" effect="light" content="ËæìÂÖ•ÂëòÂ∑•ÂêçÂ≠óËøõË°åÊ®°Á≥äÊêúÁ¥¢" placement="top">
                    <el-select v-model="adjust.eid" style="width: 200px" size="mini"
                               placeholder="ËØ∑ËæìÂÖ•ÂëòÂ∑•ÂßìÂêçÊêúÁ¥¢"
                               :remote-method="remoteMethod"
                               filterable
                               remote>
                      <el-option
                        v-for="item in emps"
                        :key="item.id"
                        :label="item.name"
                        :value="item.id">
                      </el-option>
                    </el-select>
                  </el-tooltip>
                </el-form-item>
              </div>
            </el-col>
            <el-col :span="6">
              <div>
                <el-form-item label="Â•ñÊÉ©Êó•Êúü:" prop="asDate">
                  <el-date-picker
                    v-model="adjust.asDate"
                    value-format="yyyy-MM-dd HH:mm:ss"
                    size="mini"
                    style="width: 135px"
                    type="date"
                    placeholder="ËØ∑ËæìÂÖ•Â•ñÊÉ©Êó•Êúü">
                  </el-date-picker>
                </el-form-item>
              </div>
            </el-col>
            <el-col :span="6">
              <div>
                <el-form-item label="Â•ñÊÉ©ÂéüÂõ†:" prop="reason">
                  <el-input prefix-icon="el-icon-edit" v-model="adjust.reason" size="mini"
                            style="width: 150px"
                            placeholder="ËØ∑ËæìÂÖ•Â•ñÊÉ©ÂéüÂõ†"></el-input>
                </el-form-item>
              </div>
            </el-col>
            <el-col :span="6">
              <div>
                <el-form-item label="Â•ñÊÉ©ÈáëÈ¢ù:" prop="amount">
                  <el-input prefix-icon="el-icon-edit" v-model="adjust.amount" size="mini"
                            style="width: 150px"
                            placeholder="ËØ∑ËæìÂÖ•Â•ñÊÉ©ÈáëÈ¢ù"></el-input>
                </el-form-item>
              </div>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="6">
              <div>
                <el-form-item label="Â§áÊ≥®:" prop="remark">
                  <el-input prefix-icon="el-icon-edit" v-model="adjust.remark" size="mini"
                            style="width: 150px"
                            placeholder="ËØ∑ËæìÂÖ•Â§áÊ≥®"></el-input>
                </el-form-item>
              </div>
            </el-col>
          </el-row>
          <!--Á¨¨‰∫åË°å-->
          <span slot="footer" class="dialog-footer">
              <el-button size="mini" @click="cancelEidt">Âèñ Ê∂à</el-button>
              <el-button size="mini" type="primary"
                         @click="addAdjustSalary('addAdjustSalary')">Á°Æ ÂÆö</el-button>
            </span>
        </el-dialog>
      </el-form>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        keywords: '',
        totalCount: -1,
        currentPage: 1,
        dialogVisible: false,
        dialogTitle: '',
        tableLoading: false,
        index: 0,
        size: 10,
        emps: [],
        adjusts: [],
        adjust: {
          id: '',
          eid: '',
          asDate: '',
          amount: '',
          reason: '',
          remark: '',
          name: ''
        },
        multipleSelection: [],
        rules: {
          eid: [{required: true, message: 'ÂøÖÂ°´:ÂßìÂêç', trigger: 'blur'}],
          asDate: [{required: true, message: 'ÂøÖÂ°´:Â•ñÊÉ©Êó•Êúü', trigger: 'blur'}],
          amount: [{required: true, message: 'ÂøÖÂ°´:Â•ñÊÉ©ÈáëÈ¢ù', trigger: 'blur'}],
          reason: [{required: true, message: 'ÂøÖÂ°´:Â•ñÊÉ©ÂéüÂõ†', trigger: 'blur'}],
          remark: [{required: true, message: 'ÂøÖÂ°´:Â§áÊ≥®', trigger: 'blur'}],
        },
      };
    },
    methods: {
      addAdjustSalaryInfo() {
        this.dialogTitle = 'Êñ∞Â¢ûÂ•ñÊÉ©';
        this.dialogVisible = true;
      },
      keywordsChange(val) {
        if (val === '') {
          this.loadAdjustSalaryInfo();
        }
      },
      search() {
        //ÊêúÁ¥¢ÂàôËÆæÁΩÆÂΩìÂâçÈ°µ‰∏∫Á¨¨‰∏ÄÈ°µ
        this.currentPage = 1;
        this.loadAdjustSalaryInfo();
      },
      remoteMethod(query) {
        if (query !== '') {
          this.getRequest('/per/emp/?size=10&name=' + query).then(resp => {
            if (resp.status === 200) {
              this.emps = resp.obj.data;
            }
          })
        }
      },
      currentChange(currentChange) {
        this.currentPage = currentChange;
        this.loadAdjustSalaryInfo();
      },
      deleteAll() {
        this.$confirm('Âà†Èô§[' + this.multipleSelection.length + ']Êù°ËÆ∞ÂΩï, ÊòØÂê¶ÁªßÁª≠?', 'ÊèêÁ§∫', {
          confirmButtonText: 'Á°ÆÂÆö',
          cancelButtonText: 'ÂèñÊ∂à',
          type: 'warning'
        }).then(() => {
          var ids = '';
          this.multipleSelection.forEach(row => {
            ids = ids + row.id + ",";
          })
          this.doDelete(ids);
        }).catch(() => {
          this.$message({
            type: 'info',
            message: 'Â∑≤ÂèñÊ∂àÂà†Èô§'
          });
        });
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleEdit(index, row) {
        this.dialogTitle = 'ÁºñËæëÂ•ñÊÉ©‰ø°ÊÅØ';
        this.dialogVisible = true;
        this.adjust = row;
      },
      commit(index, row) {
        this.$confirm('Êèê‰∫§ÁîüÊïàËøôÊù°ËÆ∞ÂΩï, ÊòØÂê¶ÁªßÁª≠?', 'ÊèêÁ§∫', {
          confirmButtonText: 'Á°ÆÂÆö',
          cancelButtonText: 'ÂèñÊ∂à',
          type: 'warning'
        }).then(() => {
          this.doCommit(row.id);
        }).catch(() => {
          this.$message({
            type: 'info',
            message: 'Â∑≤ÂèñÊ∂àÂà†Èô§'
          });
        });
      }
      ,
      handleDelete(index, row) {
        this.$confirm('Âà†Èô§[' + 1 + ']Êù°ËÆ∞ÂΩï, ÊòØÂê¶ÁªßÁª≠?', 'ÊèêÁ§∫', {
          confirmButtonText: 'Á°ÆÂÆö',
          cancelButtonText: 'ÂèñÊ∂à',
          type: 'warning'
        }).then(() => {
          this.doDelete(row.id);
        }).catch(() => {
          this.$message({
            type: 'info',
            message: 'Â∑≤ÂèñÊ∂àÂà†Èô§'
          });
        });
      },
      doCommit(id) {
        var _this = this;
        _this.tableLoading = true;
        this.postRequest("/sal/adjust/commit/" + id).then(resp => {
          _this.tableLoading = false;
          if (resp && resp.status === 200) {
            var data = resp.data;
            _this.loadAdjustSalaryInfo();
          }
        });
      }
      ,
      doDelete(id) {
        var _this = this;
        _this.tableLoading = true;
        this.deleteRequest("/sal/adjust/" + id).then(resp => {
          _this.tableLoading = false;
          if (resp && resp.status === 200) {
            var data = resp.data;
            _this.loadAdjustSalaryInfo();
          }
        });
      },
      loadAdjustSalaryInfo() {
        //ÂàùÂßãÂåñÊï∞ÊçÆ
        this.tableLoading = true;
        var _this = this;
        this.getRequest(
          "/sal/adjust/?name=" + _this.keywords + "&page=" + this.currentPage + "&size="
          + this.size).then(resp => {
          _this.tableLoading = false;
          if (resp && resp.status === 200) {
            _this.adjusts = resp.obj.data;
            _this.totalCount = resp.obj.total;
            //ÂΩìÂâçÈ°µÁ†Å Ê≤°ÂèñÂà∞Êï∞ÊçÆ
            if (this.adjusts.length === 0) {
              this.currentPage = 1;
            }
          }
          var old = _this.adjusts;
          old.forEach(item => {
            _this.emps.forEach(itemE => {
              if (item.eid === itemE.id) {
                item.name = itemE.name;
              }
            })
          })
          _this.adjusts = old;
        })
      },
      emptyAdjustSalaryInfo() {
        this.adjust = {
          id: '',
          eid: '',
          afterDepId: '',
          afterJobId: '',
          removeDate: '',
          reason: '',
          remark: '',
          name: ''
        };
        this.index = 0;
      },
      cancelEidt() {
        this.dialogVisible = false;
        this.emptyAdjustSalaryInfo();
      },
      addAdjustSalary(formName) {
        var _this = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            //Â∑•ÂÖ∑move.idÊòØÂê¶Â≠òÂú®
            if (this.adjust.id) {
              //Êõ¥Êñ∞
              this.tableLoading = true;
              this.putRequest("/sal/adjust/", this.adjust).then(resp => {
                _this.tableLoading = false;
                if (resp && resp.status === 200) {
                  var data = resp.data;
                  _this.dialogVisible = false;
                  _this.emptyAdjustSalaryInfo();
                  _this.loadAdjustSalaryInfo();
                }
              })
            } else {
              //Ê∑ªÂä†
              this.tableLoading = true;
              this.postRequest("/sal/adjust/", this.adjust).then(resp => {
                _this.tableLoading = false;
                if (resp && resp.status === 200) {
                  var data = resp.data;
                  _this.dialogVisible = false;
                  _this.emptyAdjustSalaryInfo();
                  _this.loadAdjustSalaryInfo();
                }
              })
            }
          } else {
            return false;
          }
        });
      },
      initData() {
        var _this = this;
        _this.loadAdjustSalaryInfo();
      }
    }
    ,
    mounted: function () {
      this.initData();
    }
  }
</script>
<style>
  .el-dialog__body {
    padding-bottom: 10px;
  }
</style>
