<script>
  export default {
    name: "SalSobCfg",
    data() {
      return {
        emps: [],
        total: 0,
        currentPage: 1,
        currentSize: 10,
        currentSalary: null,
        salaries: [],
        loading: true,
        keyword: ''
      }
    },
    mounted() {
      this.initEmps();
      this.initSalaries();
    },
    methods: {
      search() {
        this.page = 1;
        this.initEmps();
      },
      sizeChange(size) {
        this.currentSize = size;
        this.initEmps();
      },
      currentChange(page) {
        this.currentPage = page;
        this.initEmps();
      },
      hidePop(data) {
        if (this.currentSalary) {
          this.putRequest('/sal/sobcfg/?eid=' + data.id + '&sid=' + this.currentSalary).then(
            resp => {
              if (resp) {
                this.initEmps()
              }
            });
        }
      },
      showPop(data) {
        if (data) {
          this.currentSalary = data.id;
        } else {
          this.currentSalary = null;
        }
      },
      initSalaries() {
        this.getRequest("/sal/sobcfg/salaries").then(resp => {
          if (resp) {
            this.salaries = resp;
            this.loading = false;
          }
        })
      },
      initEmps() {
        this.getRequest("/sal/sobcfg/?page=" + this.currentPage + '&size=' + this.currentSize+'&keyword='+this.keyword).then(
          resp => {
            if (resp) {
              this.emps = resp.data;
              this.total = resp.total;
            }
          })
      }
    }
  }
</script>

